.container{
    border:1px solid rgba(0, 0, 0, 0);
    box-shadow: inset 1px 1px 1px rgba(173, 172, 172, 0.137);
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    background-color: none;
    background-color: rgba(0, 0, 0, 0.062);
    margin-bottom: 20px;
    border-radius: 15px;
}
input,
select{
    height: 35px;  
    border: none;
    font-size: 15px;
    font-weight: 600;
    background-color: rgba(0, 0, 0, 0);
    color: black;
    
}
input,
select:focus{
    outline: 0;
    
}
select{
    width: 30%;
}
input{
    width: 70%;
}

