.container{
  height: 100vh;
  background-color: rgb(199, 221, 253);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
.wrapper{
  padding: 10px 40px;
  background-color: rgba(255, 255, 255, 0.733);
  z-index: 2;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  border-radius: 20px;
}
h1{
  color: black;
  text-align: center;

}

.arrow_reverse{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;

}
@media(max-width:600px){
  .wrapper{
    padding: 10px 40px;
    background-color: rgba(255, 255, 255, 0.733);
    z-index: 2;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    border-radius: 20px;
    width: 70%;
  }
  h1{
  color: black;
  text-align: center;

}
}